.btn2{
    display: inline-block;
    width: 120px;
    height: 120px;
    background: #F0F2F5;
    margin: 10px;
    border-radius: 100%;
    /*box-shadow: 0 5px 15px -5px #00000070;*/
    color: #19799E;
    overflow: hidden;
    position: relative;
    text-align: center;
  }
  .btn2 i{
    line-height: 120px;
    font-size: 40px;
    transition: 0.2s linear;
  }
  .btn2:hover i{
    transform: scale(1.3);
    color: #F0F2F5;
  }
  .btn2::before{
    content: "";
    position: absolute;
    width: 120%;
    height: 120%;
    background: #19799E;
    transform: rotate(45deg);
    left: -110%;
    top: 90%;
  }
  .btn2:hover::before{
    -webkit-animation: aaa 0.7s 1;
            animation: aaa 0.7s 1;
    top: -10%;
    left: -10%;
  }
  @-webkit-keyframes aaa {
    0%{
      left: -110%;
      top: 90%;
    }50%{
      left: 10%;
      top: -30%;
    }100%{
      top: -10%;
      left: -10%;
    }
  }
  @keyframes aaa {
    0%{
      left: -110%;
      top: 90%;
    }50%{
      left: 10%;
      top: -30%;
    }100%{
      top: -10%;
      left: -10%;
    }
  }
